@import url(https://fonts.googleapis.com/css2?family=Acme&family=Alegreya:wght@600&display=swap);
a{
  text-decoration: none;
  
}
.navbar_navlink__2VIVo {
  color: #EAE7DC;
  font-family: 'Alegreya', serif !important;
  font-size: larger;
  text-transform: uppercase;
  text-decoration: none !important;
  padding: 6px 0px;
  margin: 0px 6px;
  display: inline-block;
  position: relative;
  opacity: 0.75;
}
.navbar_navlink__2VIVo:hover {
  opacity: 1;
  color: #EAE7DC;
}
.navbar_navlink__2VIVo::before {
  width: 100%;
  /* bottom: 15px; */
  top: 15px;
  opacity: 0;
  transition: 300ms;
  height: 4px;
  content: "";
  position: absolute;
  background-color: #EAE7DC;
}
.navbar_navlink__2VIVo:hover::before {
  /* bottom: 40px; */
  top: 40px;
  opacity: 1;
}
@media only screen and (max-width: 1024px) {
  .navbar_navlink__2VIVo {
    font-size: small;
  }
  .navbar_navlink__2VIVo:hover::before {
    /* bottom: 40px; */
    top: 25px;
    opacity: 1;
  }
}
